<template>
<div class="search">
	<div class="search-head">
		<div class="back" @click="$router.push({name: 'Home'})">
			<i class="iconfont icon-fanhui"></i>
		</div>
		<div class="search-box">
			<van-icon class="search-icon" size="16px" color="#ccc" name="search" />
			<input class="search-inp" type="text" placeholder="请输入商品名称" v-model="text" @input="searchItem">
		</div>
	</div>
	
	<div class="search-list">
		<ul>
			<li class="search-item border-bottom-1px" v-for="item in searchList" :key="item.id" @click="showDetailHandle(item.pid)">
				<h3 class="name">{{item.name}}</h3>
			</li>
		</ul>
	</div>
</div>
</template>

<script>
import '../assets/scss/search.scss'

export default {
	data() {
		return {
			text: '',
			searchList: []
		}
	},
	methods: {
		// 查看商品
		showDetailHandle(pid) {
			this.$router.push({
				name: 'Detail',
				params: {
					id: pid
				}
			})
		},
		searchBySever() {
			this.timer = setTimeout(() => {
				
				let params = {
					appkey: this.appkey,
					name: this.text
				}

				this.axios({
					method: 'GET',
					url: '/search',
					params
				})
				.then(res => {
					console.log(res);
					if(res.data.code === 'Q001') {
						this.searchList = [];
						if(res.data.result.length > 0) {
							this.searchList = res.data.result;
						}
					}
					
				})
				.catch(err => {
					console.log(err);
					
				})

			},500)
		},
		// 搜索
		searchItem() {

			if(!this.text) {
				this.searchList = [];
				clearTimeout(this.timer);
				return ;
			}

			// 防抖
			if(this.timer) {
				clearTimeout(this.timer);
				this.searchBySever();
				return ;
			}

			this.searchBySever();		
		},
		onClickLeft() {
			this.$router.go(-1);
		}
	}
}
</script>
